import React, { useEffect, useMemo, useState } from 'react';
import { ethers } from 'ethers';
import { useWeb3React } from '@web3-react/core/core/core';
import { Button, Flex } from '@pancakeswap/uikit';
import { Container, HeaderContainer, Title, ImageGrid, DepositList, ArrowButton, InfoText } from './StyledComponents2';
import tokenABI from './TokenABI.json';
import GiftABI from './GiftABI.json';

const VizslaSwap = () => {
  const { account } = useWeb3React();
  const [tokenContract, setTokenContract] = useState(null);
  const [vizslaSwapContract, setVizslaSwapContract] = useState(null);

  const tokenContractAddress = '0xaDAaE082237cB1772c9e079dB95c117E6Dd0C5aF';
  const contractAddress = '0xC8B19e19f52CE07bf9c4745caA4aAC87ab199cDD';

  const [approved, setApproved] = useState(false);
  const [showDropdown, setShowDropdown] = useState(false);
  const [loadingIndex, setLoadingIndex] = useState<number | null>(null);
  const [deposits, setDeposits] = useState([]);
  const [currentPage, setCurrentPage] = useState(0);

  const depositOptions = useMemo(() => [
    {
      amount: ethers.utils.parseUnits('200', 18),
      label: '200 VIZSLASWAP ğŸ',
      color: 'linear-gradient(135deg, #62D5A3 0%, #38A169 100%)', // ZÃ¶ld gradiens
    },
    {
      amount: ethers.utils.parseUnits('500', 18),
      label: '500 VIZSLASWAP ğŸ',
      color: 'linear-gradient(135deg, #CD7F32 0%, #8C4A00 100%)', // Bronz gradiens
    },
    {
      amount: ethers.utils.parseUnits('1000', 18),
      label: '1000 VIZSLASWAP ğŸ',
      color: 'linear-gradient(135deg, #6E6E6E 0%, #4B4B4B 100%)', // EzÃ¼st gradiens
    },
    {
      amount: ethers.utils.parseUnits('5000', 18),
      label: '5000 VIZSLASWAP ğŸ',
      color: 'linear-gradient(135deg, #B8860B 0%, #8B7500 100%)', // Arany gradiens
    },
  ], []);

  useEffect(() => {
    const initializeContracts = async () => {
      if (account) {
        try {
          const provider = new ethers.providers.Web3Provider(window.ethereum);
          const signer = provider.getSigner();

          setTokenContract(new ethers.Contract(tokenContractAddress, tokenABI, signer));
          setVizslaSwapContract(new ethers.Contract(contractAddress, GiftABI, signer));
        } catch (error) {
          console.error('Failed to initialize contracts:', error);
        }
      }
    };

    initializeContracts();
  }, [account]);

  useEffect(() => {
    const checkApprovalStatus = async () => {
      if (tokenContract && account) {
        try {
          const maxAmount = ethers.utils.parseUnits('5000', 18);
          const allowance = await tokenContract.allowance(account, contractAddress);
          setApproved(allowance.gte(maxAmount));
        } catch (error) {
          console.error('Error checking allowance:', error);
        }
      }
    };

    checkApprovalStatus();
  }, [account, tokenContract]);

  useEffect(() => {
    const fetchDeposits = async () => {
      if (vizslaSwapContract) {
        try {
          let i = currentPage * 5;
          const promises = [];

          while (true) {
            const currentIndex = i;
            promises.push(
              vizslaSwapContract.getDepositDetails(currentIndex)
                .then(deposit => ({ ...deposit, id: currentIndex }))
                .catch(error => {
                  console.error('Stopping fetch due to error:', error);
                  return null;
                })
            );
            i++;
            if (i >= currentPage * 5 + 5) break;
          }

          const fetchedDeposits = await Promise.all(promises);
          const validDeposits = fetchedDeposits.filter(deposit => deposit !== null);

          const sortedDeposits = validDeposits.reverse();

          setDeposits(sortedDeposits.map((deposit) => ({
            user: deposit[0],
            amount: deposit[1],
            timestamp: deposit[2],
            transactionId: deposit.id,
          })));
        } catch (error) {
          console.error('Error fetching deposits:', error);
        }
      }
    };

    if (showDropdown) {
      fetchDeposits();
    }
  }, [currentPage, vizslaSwapContract, showDropdown]);

  const approve = async (index: number) => {
    if (tokenContract && account) {
      try {
        const tx = await tokenContract.approve(contractAddress, depositOptions[index].amount);
        setLoadingIndex(index);
        await tx.wait();
        const allowance = await tokenContract.allowance(account, contractAddress);
        if (allowance.gte(depositOptions[index].amount)) {
          setApproved(true);
        }
      } catch (error) {
        console.error('Approval failed:', error);
      } finally {
        setLoadingIndex(null);
      }
    }
  };

  const deposit = async (index: number) => {
    if (vizslaSwapContract) {
      try {
        setLoadingIndex(index);
        const tx = await vizslaSwapContract.makeDeposit(index + 1);
        await tx.wait();
        alert('Deposit successful!');
        setShowDropdown(false);
      } catch (error) {
        console.error('Deposit failed:', error);
      } finally {
        setLoadingIndex(null);
      }
    }
  };

  const handleButtonClick = async (index: number) => {
    if (!approved) {
      await approve(index);
    } else {
      await deposit(index);
    }
  };

  return (
    <Flex alignItems="right" justifyContent="right">
      <Container>
        <HeaderContainer onClick={() => setShowDropdown(!showDropdown)}>
          <InfoText>Send Gift ğŸ</InfoText>
        </HeaderContainer>

        {showDropdown && (
          <>
            <ImageGrid style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '10px', marginBottom: '20px' }}>
              {depositOptions.map((option, index) => (
                <Button
                  key={option.amount.toString()}
                  disabled={loadingIndex !== null}
                  onClick={() => handleButtonClick(index)}
                  style={{
                    background: option.color,
                    padding: '10px',
                    fontSize: '12px',
                    margin: '5px 0',
                    textAlign: 'center',
                  }}
                >
                  {loadingIndex === index ? 'Processing...' : approved ? option.label : 'Approve'}
                </Button>
              ))}
            </ImageGrid>

            <Title style={{ textAlign: 'center' }}>Recent Deposits</Title>
            <DepositList>
              {deposits.map((deposit) => (
                <li key={deposit.transactionId}>
                  <strong>User:</strong> {deposit.user} <br />
                  <strong>Amount:</strong> {ethers.utils.formatUnits(deposit.amount, 18)} VIZSLASWAP <br />
                  <strong>Timestamp:</strong> {new Date(deposit.timestamp * 1000).toLocaleString()}
                </li>
              ))}
            </DepositList>

            <div style={{ display: 'flex', justifyContent: 'space-between', marginTop: '10px' }}>
              <ArrowButton onClick={() => setCurrentPage((prev) => Math.max(prev - 1, 0))} disabled={currentPage === 0}>
                &larr; Previous
              </ArrowButton>
              <ArrowButton onClick={() => setCurrentPage((prev) => prev + 1)} disabled={deposits.length < 5}>
                Next &rarr;
              </ArrowButton>
            </div>
          </>
        )}
      </Container>
    </Flex>
  );
};

export default VizslaSwap;
